#board,
#playerCards {
    display: block;
    clear: both;
    width: 100%;
    min-height: 240px;
}

.checkbox-big{
    width:50px !important;
    height:25px;
}
.form-text2{
    margin-left: 10px;
}
.absolute{
    position: absolute;
   left: 50px;
   top:100px;
}

.poker-card{
    background: #3993ff;
    background: linear-gradient(45deg,#3993ff 12%,transparent 0,transparent 88%,#3993ff 0),linear-gradient(135deg,transparent 37%,#1a7bf2 0,#1a7bf2 63%,transparent 0),linear-gradient(45deg,transparent 37%,#3993ff 0,#3993ff 63%,transparent 0),#74b3ff;
    background-size: 17px 17px;
    border: 1px solid hsla(204,6%,68%,.4);
    border-radius: 0.6rem;
    height: 6rem;
    width: 3.5rem;
}

.poker-card-hidden{
    align-items: center;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    background: #e8e9ea;
    background-size: 17px 17px;
    border: 1px solid hsla(204,6%,68%,.4);
    border-radius: 0.6rem;
    box-shadow: 0 2px 4px hsl(204deg 6% 68% / 40%);
    display: flex;
    height: 6rem;
    justify-content: center;
    overflow: hidden;
    width: 3.5rem;
}

.poker-card-voted {
    align-items: center;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    background: #fff;
    border: 2px solid #3993ff;
    border-radius: 0.6rem;
    color: #3993ff;
    display: flex;
    font-weight: 700;
    height: 6rem;
    justify-content: center;
    overflow: hidden;
    width: 3.5rem;

}

.card-button {
    align-items: center;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    background: #fff;
    border: 2px solid #3993ff;
    border-radius: 0.6rem;
    color: #3993ff;
    display: flex;
    font-weight: 700;
    height: 6rem;
    justify-content: center;
    overflow: hidden;
    width: 3.5rem;
    transition-duration: 150ms;
    transition-property: all;
}

.card-li {
    display: inline-block;
    margin: 0 0.8rem;
    transition: all .1s linear;
    vertical-align: top;
    white-space: nowrap;
    transition-duration: 150ms;
    transition-property: all;
}
.card-font{
    font-size: large;
}

.card-li:hover{
    transform: translate(0, -10px);
}
.card-button:hover{
    background-color: #e1e5e9;
}
.card-button:focus{
    transform: translate(0, -20px);
    background-color: #3993ff;
    color: #fff;
}
